.CT-Information-line {
    background-position-x: 60%;
    background-position-y: 0%;
    background-repeat: no-repeat;
    z-index: 0;
    @media (max-width: 600px) {
    background-size: contain;
       background-position-x: 60%;
       background-position-y: 40%;
    }
}
.CT-zIndex{
    z-index: 9999;
}

.CT-LayerIV {
    bottom: 0%;
    left: 8%;
    @media (max-width: 540px) {
        display: none;
    }
}
.CT-LayerV {
    bottom: -10%;
    left: 14%;
    @media (max-width: 540px) {
        display: none;
    }
}
.CT-LayerVI {
    bottom: -11%;
    right: 12%;
    @media (max-width: 540px) {
        bottom: 35%;
        right: 2%;
        height: 100%;
        width: 100%;
    }
}
.CT-LayerVII {
    bottom: 2%;
    right: 6%;
    @media (max-width: 540px) {
           bottom: 0%;
           right: 6%;
           width: 90%;
           height: 100%;
    }
    
}
