@import './vars.sass';
@import "./colors.sass";
// General ===START
.uCT-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }
.uCT-click {
  cursor: pointer; }
.uCT-brightness {
  &:hover {
    filter: brightness(0.9); } }
/* width */
::-webkit-scrollbar {
  width: 5px;
  height: 15px;
  position: sticky !important; }

/* Track */
::-webkit-scrollbar-track {
  background: $support1;
  border-radius: 10px;
  position: sticky !important; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: $main;
  border-radius: 10px;
  position: sticky !important; }

// a Link Tage
a {
  // color: unset !important
  &:hover {
    text-decoration: none; } }


// General ===END

//// RADIUS ===START
.uCT-f-radius {
  border-radius: 5px; }
.uCT-f-radius-sA {
  border-radius: $microSizeA; }
.uCT-f-radius-smid-A {
  border-radius: $microSizeA + 2; }
.uCT-t-radius-sA {
  border-radius: $microSizeA $microSizeA 0 0; }
.uCT-r-radius-sA {
  border-radius: 0 $microSizeA $microSizeA 0; }
.uCT-l-radius-sA {
  border-radius: $microSizeA 0 0 $microSizeA; }
.uCT-b-radius-sA {
  border-radius: 0 0 $microSizeA $microSizeA; }

.uCT-f-radius-sB {
  border-radius: $microSizeB; }
.uCT-t-radius-sB {
  border-radius: $microSizeB $microSizeB 0 0; }
.uCT-r-radius-sB {
  border-radius: 0 $microSizeB $microSizeB 0; }
.uCT-l-radius-sB {
  border-radius: $microSizeB 0 0 $microSizeB; }
.uCT-b-radius-sB {
  border-radius: 0 0 $microSizeB $microSizeB; }
.uCT-bb-radius-sB {
  border-radius: 0 0 $microSizeB 0; }
.uCT-bbb-radius-sB {
  border-radius: $microSizeB $microSizeB 0 $microSizeB; }

.uCT-f-radius-sC {
  border-radius: $microSizeC; }
.uCT-t-radius-sC {
  border-radius: $microSizeC $microSizeC 0 0; }
.uCT-r-radius-sC {
  border-radius: 0 $microSizeC $microSizeC 0; }
.uCT-l-radius-sC {
  border-radius: $microSizeC 0 0 $microSizeC; }
.uCT-l-radius-sC {
  border-radius: $microSizeC - 4 0 0 $microSizeC - 4; }
.uCT-b-radius-sC {
  border-radius: 0 0 $microSizeC $microSizeC; }
.uCT-top-r-radius-sC {
  border-radius: 0 $microSizeC 0 0; }
.uCT-bottom-r-radius-sC {
  border-radius: 0 0 $microSizeC 0; }

// RADIUS ===END

// Background Size===START
.uCT-img-30 {
  background-size: 30%;
  background-repeat: no-repeat;
  background-position: center; }

.uCT-img-100 {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center; }

.uCT-img-contain {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center; }

.uCT-img-cover {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center; }
.uCT-img-parallax {
  background-attachment: fixed; }
.uCT-img-inherit {
  background-color: inherit; }
// Background Size===END

// app Cards ===START
.uCT-card {
  min-width: 300px;
  max-width: 339px;
  height: 486px; }

.uCT-mini-card {
  width: 100%;
  min-width: 300px;
  max-width: 339px; }

.uCT-card-grid-holder {
  align-items: center;
  display: grid;
  justify-content: center !important;
  grid-template-columns: repeat(auto-fill, minmax(300px,auto));
  grid-gap: 24px; }
.uCT-card-grid-holder-slider {
  align-items: center;
  display: grid;
  justify-content: center !important;
  // grid-template-columns: repeat(auto-fill, minmax(339px,auto))
  grid-template-columns: 339px 339px 339px 339px;
  grid-gap: 24px; }

.uCT-Text-ellipsis {
  display: inline-block;
  height: 18px;
  min-width: 50%;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.uCT-mini-dummy {
  min-width: 339px;
  height: 0; }
.uCT-review-dummy {
  min-width: 330px;
  height: 0; }
.uCT-mini-SM-list-card {
  min-width: 410px;
  max-width: 410px; }
.uCT-mini-list-dummy {
  min-width: 339px;
  height: 0; }
// app Cards ===END

// Border Size===START
.uCT-bd-white-sA { // white
  border: 2px solid $white; }
.uCT-bd-white-sB { // white
  border: 3px solid $white; }
.uCT-bd-support1-sA { //support1
  border: 2px solid $support1; }
.uCT-bd-support1-sB { //support1
  border: 3px solid $support1; }
.uCT-bd-main-sA { // main
  border: 2px solid $main; }
.uCT-bd-main-sB { //support1
  border: 3px solid $main; }

.uCT-border-bottom-b {
  border-bottom: 1px solid $main; }

.uCT-border-bottom {
  border-bottom: 3px solid $opacity-main-40; }

.uCT-border-r {
  border-right: 3px solid $opacity-main-40; }

.uCT-border-l-none {
  border-top: 1px solid $main;
  border-right: 1px solid $main;
  border-bottom: 1px solid $main;
  border-left: 0px solid $main; }

.uCT-border-r-none {
  border-top: 1px solid $main;
  border-right: 0px solid $main;
  border-bottom: 1px solid $main;
  border-left: 1px solid $main; }

.uCT-border-b-none {
  border-top: 0px solid $opacity-main-40;
  border-right: 3px solid $opacity-main-40;
  border-bottom: 3px solid $opacity-main-40;
  border-left: 3px solid $opacity-main-40; }

.uCT-border-b-none {
  border-top: 3px solid $opacity-main-40;
  border-right: 3px solid $opacity-main-40;
  border-bottom: 0px solid $opacity-main-40;
  border-left: 3px solid $opacity-main-40; }

.uCT-border-sA {
  border: 1px solid $main; }
.uCT-border-secondary-sA {
  border: 1px solid $secondary; }
.uCT-border-sB-main {
  border: 2px solid $main; }
.uCT-border-smid-A {
  border: 1px solid $support2; }

.uCT-border-sB {
  border: 2px solid $support1; }

.uCT-border-smid-B {
  border: 2px solid $smoke; }

.uCT-border-sc {
  border: 4px solid $support1; }

.uCT-border-sd {
  border-left: 4px solid $opacity-main-40;
  height: 80%; }
// app Cards ===END
// app widget ===START
.uCT-right-widget {
  width: 362px;
  position: sticky;
  top: 75px;
  @media ( max-width: 992px ) {
    position: static; } }

// app widget ===END

.uCT-support1-logo {
  width: 100%;
  height: $microSizeG;
  background-position: left !important; }

.uCT-Dark-logo {
  width: 162px;
  height: 56px;
  background-position: center !important; }

.uCT-Man-Virtual {
  height: 100%; }

.uCT-input-outline-none {
  outline: none;
  border: none; }

.uCT-Custom-Width {
  height: $microSizeE;
  max-width: 600px; }

.uCT-Active-Border-B {
  border-bottom: 1px solid $main; }

.uCT-Password-Bar {
  width: $microSizeF;
  height: 4px; }
//Social Media Function opacity
.uCT-bg {
  @include cCT-main-bg(.1px); }

.uCT-Social-bg {
  @include cCT-main-bg(.2px); }

.uCT-googlePlay {
  @include cCT-gray-bg(0.05px); }

.uCT-AppStore {
  @include cCT-gray-bg(0.05px); }

// Agencies Listing
.uCT-cardImg-holder {
  width: 190px;
  height: 190px; }

.uCT-StarRate-Holder {
  width: 104px;
  height: 32px; }

.uCT-StarRate-position {
  position: absolute;
  top: 90%;
  left: 43px; }

.uCT-icon-holder {
  width: 40px;
  height: 40px; }

.uCT-Horizontal-Line {
  background-color: $white;
  width: 2px;
  height: 27px;
  border-radius: 20px; }

// elements
::placeholder {
  opacity: .6; }

.uCT-opacity-95 {
  opacity: 0.95; }
.uCT-wire-frame-opacity-5 {
  opacity: 0.5; }
// animations
.uCT-t-6-all {
  transition: ease-in-out .600s all; }
.uCT-t-4-all {
  transition: ease-in-out .400s all; }
.uCT-t-10-all {
  transition: ease-in-out 1s all; }
.uCT-t-2-all {
  transition: ease-in-out .200s all; }
.uCT-t-6-L {
  transition: linear .400s all; }
.uCT-scale-in-animation {
  transition: all 0.2s ease-in-out;
  &:hover {
    transform: scale(0.98,0.98);
    transition: all 0.4s ease-in-out; } }
// grid
.uCT-cards-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 24px; }

.uCT-form-border-foucs {
  border-color: $secondary; }

.uCT-form-border-blur {
  border-color: $smoke; }
.uCT-content-with-widget {
  width: calc(100% - 362px);
  @media ( max-width: 992px ) {
    width: 100%; } }

.uCT-space-24 {
  width: 1px; }

// Rotate Section
.CT-rotate-icon-l {
  transform: rotate(-180deg); }
.CT-rotate-icon-r {
  transform: rotate(0deg); }

// Ellipsis Sections
.uCT-ellipsis-1-line {
  width: 100%;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis; }
.uCT-ellipsis-2-line {
  width: 100%;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis; }
