.sas {
    position: absolute;
    width: 90%;
    aspect-ratio: 2 / 1;
    left: 27%;
    bottom: 60px;
    transform: scale3d(0.89, 0.9, 1.9);

    @if screen {
        @media (min-width:0px) and (max-width: 400px) {
            position: absolute;
            width: 100%;
            aspect-ratio: 1 / 1;
            left: 10%;
            bottom: 0px;

            @if screen {
                @media (height: 568px) {
                    left: 10%;
                }
            }
        }

        @media (min-width:401px) and (max-width: 609px) {
            position: absolute;
            width: 100%;
            // height: 80%;
            aspect-ratio: 1 / 1;
            left: 10%;
            bottom: 10px;
            transform: scale3d(0.9, 1, 1.9);

            @if screen {
                @media (height: 731px) {
                    position: absolute;
                    width: 100%;
                    height: 65%;
                    aspect-ratio: 1 / 1;
                    left: 36px;
                    bottom: 10px
                }
            }

        }

        @media (min-width:610px) and (max-width: 991px) {
            position: absolute;
            width: 85%;
            aspect-ratio: 3 / 3;
            left: 22%;
            bottom: 0px
        }

        @media (min-width: 992px) and (max-width:1200px) {
            width: 100%;
            aspect-ratio: 4 / 3;
            left: 19%;
            bottom: 0px;
            opacity: 0.8;
        }




    }
}

// Title Font Size
.CT-bold-72 {
    font-size: 72px;
    font-family: Roboto-Bold;
    font-style: normal;
    line-height: initial;

    // Animation Small Screen
    @media (max-width: 991px) {
        font-size: 24px;
        animation: missionTitle .8s ease-in-out;

        @keyframes missionTitle {
            0% {
                transform: translateX(400px);
                opacity: 0;
            }

            100% {
                transform: translateX(0px);
                opacity: 1;
            }
        }
    }

    // Small Screen
    @media (min-width:401px) and (max-width: 609px) {
        font-size: 48px;
    }

    // Mid Screen
    @media (min-width:610px) and (max-width: 991px) {
        font-size: 56px;
        padding: 24px 0px 24px 16px;
    }

    // Large Screen
    @media (min-width:992px) and (max-width: 1200px) {
        font-size: 72px;
        padding: 24px 0px 0px 16px;
    }

}

// Description Font Size
.CT-vision-description-text {
    font-size: 24px;
    font-family: Roboto-Light;

    // Small Screen
    @media (min-width:0px) and (max-width: 400px) {
        font-size: 12px;

        @if screen {
            @media (height: 812px) {
                font-size: 18px;
            }
        }
    }

    // Mid Small Screen
    @media (min-width:401px) and (max-width: 609px) {
        font-size: 18px;
    }

    // Mid Screen
    @media (min-width:610px) and (max-width: 991px) {
        font-size: 24px;
        margin-left: 16px;
    }

    div:nth-of-type(1) {
        margin-bottom: 8px;

        animation: missionText-I 0.6s ease-in-out;

        @keyframes missionText-I {
            from {
                transform: translateX(170px);
            }

            to {
                transform: translateX(0px);
                z-index: 999;
            }
        }
    }

    div:nth-of-type(2) {
        margin-bottom: 8px;
        animation: missionText-II 0.8s ease-in-out;

        @keyframes missionText-II {
            from {
                transform: translateX(190px);
            }

            to {
                transform: translateX(0px);
            }
        }
    }

    div:nth-of-type(3) {
        margin-bottom: 8px;
        animation: missionText-III 1s ease-in-out;

        @keyframes missionText-III {
            from {
                transform: translateX(210px);
            }

            to {
                transform: translateX(0px);
            }
        }
    }

    div:nth-of-type(4) {
        margin-bottom: 8px;
        animation: missionText-IV 1.2s ease-in-out;

        @keyframes missionText-IV {
            from {
                transform: translateX(230px);
            }

            to {
                transform: translateX(0px);
            }
        }
    }

    div:nth-of-type(5) {
        animation: missionText-IV 1.4s ease-in-out;

        @keyframes missionText-IV {
            from {
                transform: translateX(250px);
            }

            to {
                transform: translateX(0px);
            }
        }
    }
}



.CT-our-vision-layer-shape {
    width: 100%;
    height: 110%;
    background-position: -15%;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    z-index: 0;
    bottom: -27%;
    left: -4%;

    @media (max-width: 991px) {
        width: 80%;
        height: 100%;
        background-size: contain;
        background-repeat: no-repeat;
        position: absolute;
        z-index: 0;
        top: -34%;
        left: -27%;
    }
}

.CT-ourVision-holder {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 90%;
    background-origin: content-box;
    z-index: 1;
    position: relative;
}

.CT-ourVision-line {
    position: absolute;
    z-index: -1;
    background-position: 280px 99px;
    background-repeat: no-repeat;

    // Small Screen
    @media (min-width:0px) and (max-width: 400px) {
        background-position: 42px 24px;
        background-size: 135px;
    }

    // Mid Small Screen
    @media (min-width:401px) and (max-width: 609px) {
        background-position: 155px 60px;
        background-size: 195px;
    }

    // Mid Screen
    @media (min-width:610px) and (max-width: 991px) {
        background-position: 167px 90px;
    }

    // Large Screen
    @media (min-width:992px) and (max-width: 1200px) {
        background-position: 280px 125px;
    }

    animation: visionLine 1.2s ease-in-out;

    @keyframes visionLine {
        0% {
            opacity: 0;
        }

        25% {
            opacity: 0;
        }

        50% {
            opacity: 0;
        }

        75% {
            opacity: 0.8;
        }

        100% {
            opacity: 1;
        }
    }
}

.CT-ourVision-iconSize-small {

    @media (min-width: 540px) {
        background-image: url('../../../../../assets/icons/Ellipse.svg');
    }

    @media (max-width: 991px) {
        width: 16px !important;
        height: 16px !important;
        background-color: #eef0fc;
        border-radius: 16px;
    }
}