// Mian 360 Tour Grid
.CT-360-tour-content-feature {
    grid-column: 2 / 3;
    align-items: center;
    display: flex;
    justify-content: center;

    @media (max-width: 991px) {
        grid-column: 1 / 3;
        grid-row: 1/2
    }
}

// 360 Title And Decription
.CT-360-title-and-decription-grid {
    width: 100%;
    display: grid;
    height: 100%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 35px 1fr 1fr;
    z-index: 1;

    @media (max-width: 991px) {
        width: 100%;
        gap: 16px;
    }
}

// 360Tour Title Holder Style 
.CT-360Tour-title {
    animation: _360TourAnimation 1s ease;

    @keyframes _360TourAnimation {
        from {
            transform: translateX(300px);
            opacity: 0;
            color: #000;
        }

        to {
            transform: translateX(0px);
            opacity: 1;
            color: #000;
        }
    }

    @media (max-width: 991px) {
        grid-column: 1/3;
    }
}

// Title Font Style 
.CT-bold-56 {
    font-size: 56px;
    font-family: Roboto-Bold;
    font-style: normal;

    @media (max-width: 991px) {
        font-size: 24px;
        font-family: Roboto-Bold;
        font-style: normal;
    }
}

// 360Tour Description Holder
.CT-Tour360Description-Holder {
    width: 100%;
    display: grid;
    gap: 24px;
    grid-template-columns: auto;
    grid-template-rows: repeat(4, auto);
    z-index: 1;

    @media (max-width: 991px) {
        display: contents;
        text-align: center;
    }

    div:nth-of-type(1) {
        animation: unlimitedImages 1.4s ease;

        @keyframes unlimitedImages {
            from {
                transform: translateX(900px);
                opacity: 0;
                color: #000;
            }

            to {
                transform: translateX(0px);
                opacity: 1;
                color: #000;
            }
        }
    }

    div:nth-of-type(2) {
        animation: privateCloud 1.6s ease;

        @keyframes privateCloud {
            from {
                transform: translateX(1000px);
                opacity: 0;
                color: #000;
            }

            to {
                transform: translateX(0px);
                opacity: 1;
                color: #000;
            }
        }
    }

    div:nth-of-type(3) {
        animation: remoteVR 1.8s ease;

        @keyframes remoteVR {
            from {
                transform: translateX(1100px);
                opacity: 0;
                color: #000;
            }

            to {
                transform: translateX(0px);
                opacity: 1;
                color: #000;
            }
        }
    }

    div:nth-of-type(4) {
        animation: floorPlan 2s ease;

        @keyframes floorPlan {
            from {
                transform: translateX(1200px);
                opacity: 0;
                color: #000;
            }

            to {
                transform: translateX(0px);
                opacity: 1;
                color: #000;
            }
        }
    }
}

// 360Tour-description-item
.CT-360Tour-description-item {
    min-width: 0; // to resolve an issu
    display: flex;
    align-items: center;

    @media (max-width: 991px) {
        display: block;
    }

}

// 360Tour Description Small Screen
.CT-360Tour-description-item i {
    @media (max-width: 991px) {
        width: 24px;
        height: 24px;
    }
}

.CT-360Toue-text-holder {
    p:nth-of-type(1) {
        @media (max-width: 991px) {
            font-size: 14px;
            font-family: Roboto-Bold;
            font-style: normal;
            padding: 0px 8px;
        }
    }

    p:nth-of-type(2) {
        @media (max-width: 991px) {
            font-size: 12px;
            font-family: Roboto-Regular;
            padding: 0px 8px;
        }
    }
}


.CT-Gradian-Linear-left {
    background-image: linear-gradient(to left, #f5f5ff, #e9f7fe)
}