// Our Mission Main Title
.CT-Mission-bold-144 {
    font-size: 72px;
    font-family: Roboto-Bold;
    font-style: normal;
    line-height: initial;
    transition: .2s ease-out;

    // Main Title Small Media
    @media (min-width: 0px) and (max-width: 400px) {
        font-size: 24px;
        padding-bottom: 24px;
    }

    // Main Title Mid Small Media
    @media (min-width: 401px) and (max-width: 609px) {
        font-size: 48px;
        padding-bottom: 24px;
    }

    // Main Title Mid Media
    @media (min-width: 610px) and (max-width: 991px) {
        font-size: 88px;
        padding-bottom: 24px;
    }

    // Main Title Large Media
    @media (min-width: 992px) and (max-width: 1200px) {
        font-size: 64px;
        padding: 24px;
    }

    // Main Title Animation
    animation: missionTitle .8s ease-in-out;

    @keyframes missionTitle {
        0% {
            transform: translateX(400px);
            opacity: 0;
        }

        100% {
            transform: translateX(0px);
            opacity: 1;
        }
    }
}

// Shadow Circle Image
.CT-our-mission-Circle-shape {
    width: 100%;
    height: 100%;
    background-position: bottom right;
    background-size: contain;
    background-repeat: no-repeat;
    top: 250px;
    left: 250px;

    // Circle Small Screen 
    @media (min-width: 0px) and (max-width: 400px) {
        top: 115px;
        left: 100px;
    }

    // Circle Mid Small Screen
    @media (min-width: 401px) and (max-width: 609px) {
        height: 100%;
        width: 100%;
        top: 160px;
        left: 160px;

        @if screen {
            @media (width: 540px) and (height: 720px) {
                height: 100%;
                width: 100%;
                top: 260px;
                left: 260px;
            }
        }

        @if screen {
            @media (width: 411px) and (height: 823px) {
                height: 100%;
                width: 100%;
                top: 120px;
                left: 100px;
            }
        }
    }

    // Circle Mid Screen
    @media (min-width: 610px) and (max-width: 991px) {
        height: 68%;
        top: 420px;
        left: 50px;
    }

    // Circle Large Screen
    @media (min-width: 992px) and (max-width: 1200px) {
        width: 100%;
        height: 100%;
        background-position: bottom right;
        background-size: contain;
        background-repeat: no-repeat;
        top: 300px;
        left: 300px;

    }

    // Circle Animation
    animation: missionCircle 3s ease-in-out;

    @keyframes missionCircle {
        0% {
            opacity: 0;
        }

        50% {
            opacity: .5;
        }

        100% {
            opacity: 1;
        }
    }
}

// Rocket Smooke
.CT-rocketSmoke-layer-shape {
    width: 100%;
    height: 100%;
    background-image: url('../../../../../assets/images/RocketSmoke.png');
    background-position: bottom right;
    background-repeat: no-repeat;
    right: -200px;
    bottom: -90px;
    z-index: -1;
    opacity: 0.5;

    // Smoke Small Screen
    @media (min-width: 0px) and (max-width: 400px) {
        background-size: contain;
        background-repeat: no-repeat;
        left: 52%;
        top: 17%;
        opacity: 0.5;
        z-index: 0;

    }

    // Smoke Mid Screen
    @media (min-width: 401px) and (max-width: 991px) {
        height: 40%;
        background-size: contain;
        background-repeat: no-repeat;
        left: 194px;
        bottom: -119px;
        opacity: 0.5;
        z-index: 0;
    }

    // Smoke Animation
    animation: rocketSmoke 1.2s ease;

    @keyframes rocketSmoke {
        0% {
            opacity: 0;
        }

        50% {
            opacity: 0;
            transform: perspective(500px) translate3d(-50px, -20px, -50px);
        }

        75% {
            opacity: 0.2;
        }

        100% {
            opacity: 0.5;
            transform: perspective(0px) translate3d(0px, 0px, 0px);
        }
    }
}

// Main Rocket Image
.CT-mission-rocket {
    width: 100%;
    height: 95%;
    background-position: bottom right;
    background-size: contain;
    background-repeat: no-repeat;
    right: -70px;
    bottom: -100px;
    opacity: 0.8;
    z-index: 0;

    //  Rocket Small Screen
    @media (min-width: 0px) and (max-width: 400px) {
        width: 104%;
        background-size: contain;
        background-repeat: no-repeat;
        right: -42px;
        bottom: -48px;
    }

    //  Rocket Mid Small Screen
    @media (min-width: 401px) and (max-width: 609px) {
        width: 100%;
        height: 100%;
        background-size: contain;
        background-repeat: no-repeat;
        right: -57px;
        bottom: -74px;

        @if screen {
            @media (min-height: 800px) and (max-height: 823px) {
                width: 123%;
            }
        }

        @if screen {
            @media (min-width: 540px) and (max-height: 720px) {
                width: 80%;
            }
        }
    }

    //  Rocket Mid Screen
    @media (min-width: 610px) and (max-width: 991px) {
        height: 75%;
        background-size: contain;
        background-repeat: no-repeat;
        right: -6px;
        bottom: -79px;
    }

    // Rocket Large Screen
    @media (min-width: 992px) and (max-width: 1200px) {
        width: 100%;
        height: 77%;
        background-size: contain;
        background-repeat: no-repeat;
        right: -56px;
        bottom: -90px;
    }

    // Main Rocket Animation
    animation: rocket .9s ease-in-out;

    @keyframes rocket {
        0% {
            transform: perspective(1000px) translate3d(800px, 650px, 400px);
        }

        100% {
            transform: perspective(0px) translate3d(0px, 0px, 0px);
        }
    }
}

//  Our Mission Description
.CT-mission-description-text {
    height: 70%;
    width: 65%;
    gap: 40px;
    font-size: 24px;
    font-family: 'Roboto-Light';

    // Description Small Screen
    @media (min-width: 0px) and (max-width: 400px) {
        width: 100%;
        height: 50%;
        font-size: 12px;
        padding: 0px 0px 16px 0px;
    }

    // Description Mid Small Screen
    @media (min-width: 401px) and (max-width: 610px) {
        width: 100%;
        height: 50%;
        font-size: 18px;
        padding-left: 24px;
    }

    // Description Mid Screen
    @media (min-width: 610px) and (max-width: 991px) {
        width: 100%;
        height: 50%;
        font-size: 24px;
        padding-left: 24px;
    }

    // Description Large Screen
    @media (min-width: 992px) and (max-width: 1200px) {
        width: 100%;
        height: 50%;
        font-size: 24px;
        padding-left: 32px;
        gap: 16px;
    }

    div:nth-of-type(1) {
        animation: missionText-I 0.6s ease-in-out;

        @keyframes missionText-I {
            from {
                transform: translateX(170px);
            }

            to {
                transform: translateX(0px);
                z-index: 999;
            }
        }
    }

    div:nth-of-type(2) {
        animation: missionText-II 0.8s ease-in-out;

        @keyframes missionText-II {
            from {
                transform: translateX(190px);
            }

            to {
                transform: translateX(0px);
            }
        }
    }

    div:nth-of-type(3) {
        animation: missionText-III 1s ease-in-out;

        @keyframes missionText-III {
            from {
                transform: translateX(210px);
            }

            to {
                transform: translateX(0px);
            }
        }
    }

    div:nth-of-type(4) {
        animation: missionText-IV 1.2s ease-in-out;

        @keyframes missionText-IV {
            from {
                transform: translateX(230px);
            }

            to {
                transform: translateX(0px);
            }
        }
    }

    div:nth-of-type(5) {
        animation: missionText-IV 1.4s ease-in-out;

        @keyframes missionText-IV {
            from {
                transform: translateX(250px);
            }

            to {
                transform: translateX(0px);
            }
        }
    }
}

//  Description Icon Size Small Screen
.CT-ourMission-iconSize-small {
    @media (min-width: 992px) {
        background-image: url('../../../../../assets/icons/Ellipse.svg');
    }

    @media (max-width: 991px) {
        width: 16px !important;
        height: 16px !important;
        background-color: #eef0fc;
        border-radius: 16px;
    }
}

.CT-title-and-line-holder {
    position: relative;
}

// Our Mission Line
.CT-ourMission-line {
    position: absolute;
    z-index: -1;
    background-position: 198px 75px;
    background-repeat: no-repeat;

    // Small Screen
    @media (min-width: 0px) and (max-width: 400px) {
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 40px 5px;
    }

    // Mid Small Screen
    @media (min-width: 401px) and (max-width: 609px) {
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 130px 20px;
    }

    // Mid Screen
    @media (min-width: 610px) and (max-width: 991px) {
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 280px 40px;
    }

    // Large Screen
    @media (min-width: 992px) and (max-width: 1200px) {
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 170px 40px;
    }

    animation: missionLine 1.2s ease-in-out;

    @keyframes missionLine {
        0% {
            opacity: 0;
        }

        25% {
            opacity: 0;
        }

        50% {
            opacity: 0;
        }

        75% {
            opacity: 0.8;
        }

        100% {
            opacity: 1;
        }
    }
}