.CT-who-we-are-page-holder {
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    background-image: url('../../../../assets/images/WhoWeAre.png');
    background-repeat: no-repeat;
    background-size: 50%;
    background-position: center;
    border-radius: 20px;
    animation: imageAnimate 1.4s ease-in-out;

    @keyframes imageAnimate {
        0% {
            background-position-x: left;
        }

        50% {
            background-position-x: right;
        }

        100% {
            background-position-x: center;
        }
    }

    // Who We Are Page Holder Small Screen
    @media (max-width: 991px) {
        background-image: url('../../../../assets/images/WhoWeAreSmall.png');
        background-position: center;
        background-repeat: no-repeat;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: calc(50vh - 75px) calc(50vh - 75px);
        border-radius: 0px;
        animation: imageAnimateSmall 2.4s ease-in-out;

        @keyframes imageAnimateSmall {
            0% {
                background-position-y: bottom;
            }

            50% {
                background-position-y: top;
            }

            100% {
                background-position-y: center;
            }
        }

        // Small Screen
        @if screen {
            @media (min-width: 0px) and (max-width: 609px) {
                background-size: contain;
            }
        }

        // Mid Screen
        @if screen {
            @media (min-width: 610px) and (max-width: 991px) {
                background-size: 80%;
            }
        }

    }
}


.CT-inspire-text {
    grid-column: 1/3;
    grid-row: 2/3;
    text-align: center;
    padding-bottom: 16px;
    animation: switch 2s linear;

    @keyframes switch {
        0% {
            opacity: 0;
            filter: blur(20px);
            transform: scale(1.3)
        }

        50% {
            opacity: 1;
            filter: blur(0);
            transform: scale(.9)
        }

        100% {
            opacity: 1
        }
    }

    // Inspire Text Small Screen
    @media (max-width: 991px) {
        width: 100%;
        grid-column: 1/2;
        grid-row: 1/1;
        text-align: right;
        height: 130px;
        margin-left: 30%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        padding: 0px 32px 0px 0px;
        animation: switchSmall 2s linear;

        @keyframes switchSmall {
            0% {
                opacity: 0;
                filter: blur(20px);
                transform: scale(1.3)
            }

            50% {
                opacity: 1;
                filter: blur(0);
                transform: scale(.9)
            }

            100% {
                opacity: 1
            }
        }

        // Mid Screen
        @if screen {
            @media (min-width: 500px) and (max-width: 991px) {
                height: 208px;
            }
        }

    }
}

.CT-inspire-text span {
    color: white;

    // Inspire Text Span Color Small Screen
    @media (max-width: 991px) {
        color: black;
    }
}

.CT-bold-32 {
    font-size: 32px;
    font-family: Roboto-Bold;
    font-style: normal;

    // Small Screen
    @if screen {
        @media (min-width: 0px) and (max-width: 609px) {
            font-size: 16px;
            font-family: Roboto-Bold;
            font-style: normal;
        }
    }

    @if screen {
        @media (min-width: 500px) and (max-width: 991px) {
            font-size: 32px;
        }
    }

}

.CT-Gradian-title-Main-bottom {
    animation: gradianTitleAnimation 1.5s ease-in-out;

    @keyframes gradianTitleAnimation {

        0% {
            transform: translateX(900px);
            border-radius: 20px 24px 24px 20px;
        }

        50% {
            border-radius: 20px 20px 20px 20px;
        }

        75% {
            border-radius: 20px 15px 15px 20px;
        }

        90% {
            border-radius: 20px 10px 10px 20px;
        }

        100% {
            transform: translateX(0%);
            border-radius: 20px 0px 0px 20px;
        }
    }

    // Title Gradient Small Screen 
    @media (max-width: 991px) {
        z-index: -1;
        animation: gradianTitleAnimationSmall 2.5s ease-in-out;

        @keyframes gradianTitleAnimationSmall {

            0% {
                transform: translateY(600px);
                border-radius: 20px 0px 0px 20px;

            }

            50% {
                border-radius: 20px 10px 10px 20px;

            }

            75% {
                border-radius: 20px 15px 15px 20px;
            }

            90% {
                border-radius: 10px 10px 10px 10px;

            }

            100% {
                transform: translateY(0%);
                border-radius: 0px 0px 0px 0px;

            }
        }
    }

    background-image: linear-gradient(to bottom, rgba(75, 190, 247, .12), rgba(156, 157, 254, .12))
}

.CT-Gradian-content-Main-bottom {


    animation: gradiancontentAnimation 1.5s ease-in-out;

    @keyframes gradiancontentAnimation {
        0% {
            transform: translateX(-900px);
            border-radius: 24px 20px 20px 24px;
        }

        50% {
            border-radius: 20px 20px 20px 20px;
        }

        75% {
            border-radius: 15px 20px 20px 15px;
        }

        90% {
            border-radius: 10px 20px 20px 10px;
        }

        100% {
            transform: translateX(0%);
            border-radius: 0px 20px 20px 0px;
        }
    }

    // Title Gradient Small Screen 
    @media (max-width: 991px) {
        z-index: -1;
        animation: gradiancontentAnimationSmall 2.5s ease-in-out;

        @keyframes gradiancontentAnimationSmall {
            0% {
                transform: translateY(-600px);
                border-radius: 24px 20px 20px 24px;
            }

            50% {
                border-radius: 20px 20px 20px 20px;
            }

            75% {
                border-radius: 15px 20px 20px 15px;
            }

            90% {
                border-radius: 5px 10px 10px 5px;
            }

            100% {
                transform: translateY(0%);
                border-radius: 0px 0px 0px 0px;
            }
        }
    }

    background-image: linear-gradient(to bottom, rgba(75, 190, 247, .35), rgba(156, 157, 254, .35))
}