.CT-text-content-feature {
    grid-column: 1 / 2;
    height: 65%;

    @media (max-width: 991px) {
        height: unset;
    }
}

.CT-layerI {
    position: absolute;
    width: 100%;
    height: 100%;
    top: -5%;
    left: -33%;
    z-index: -3;

    @media (max-width: 991px) {
        height: 50%;
        top: 0%;
        left: -25%;
        z-index: -3;
        animation: layerMoves 1.8s ease-in-out;

        @keyframes layerMoves {
            0% {
                transform: translateX(-100px);
                transform: translateY(-100px);
                opacity: 0;
            }

            50% {
                transform: translateX(40px);
                transform: translateY(40px);
                opacity: 1;
            }

            100% {
                transform: translateX(0px);
                transform: translateY(0px);
                opacity: 1;
            }
        }
    }

    animation: layerMove 1.8s ease-in-out;

    @keyframes layerMove {
        0% {
            transform: translateX(-400px);
            transform: translateY(-400px);
            opacity: 0;
        }

        50% {
            transform: translateX(40px);
            transform: translateY(40px);
            opacity: 1;
        }

        100% {
            transform: translateX(0px);
            transform: translateY(0px);
            opacity: 1;
            animation-iteration-count: infinite;
        }
    }
}

.CT-layerII {
    position: absolute;
    width: 100%;
    height: 100%;
    top: -8%;
    left: -40%;
    z-index: -2;
    animation: layerMove2 1.2s ease-in-out;

    @media (max-width: 991px) {
        height: 40%;
        top: 0%;
        left: -40%;
        z-index: -3;
        animation: layerMoves2 1.2s ease-in-out;

        @keyframes layerMoves2 {
            from {
                transform: translateX(-100px);
                transform: translateY(-100px);
                opacity: 1;

            }

            to {
                transform: translateX(0px);
                transform: translateY(0px);
                opacity: 1;
            }
        }
    }

    @keyframes layerMove2 {
        from {
            transform: translateX(-200px);
            transform: translateY(-200px);
            opacity: 0;

        }

        to {
            transform: translateX(0px);
            transform: translateY(0px);
            opacity: 1;
        }
    }
}

.CT-aboutus-text-animation {
    @media (max-width: 991px) {
        padding: 24px 16px 16px;
    }

    p:nth-child(1) {
        @media (max-width: 991px) {
            font-size: 24px;
            font-family: Roboto-Bold;
            font-style: normal;
            padding: 0px 0px 16px 0px;
        }

        animation: movs .4s ease-in-out;

        @keyframes movs {
            from {
                transform: translateX(100px);
                opacity: 0;
            }

            to {
                transform: translateX(0%);
                opacity: 1;
            }
        }

        @media (max-width: 991px) {
            animation: movs .4s ease-in-out;

            @keyframes movs {
                from {
                    transform: translateX(-100px);
                    opacity: 0;
                }

                to {
                    transform: translateX(0%);
                    opacity: 1;
                }
            }
        }
    }

    p:nth-child(2) {
        animation: movsTwo .6s ease-in-out;

        @keyframes movsTwo {
            from {
                transform: translateX(100px);
                opacity: 0;
            }

            to {
                transform: translateX(0%);
                opacity: 1;
            }
        }

        @media (max-width: 991px) {
            transition: 1s ease-in-out;
            width: 100%;
            font-size: 16px;

            @keyframes movsTwo {
                from {
                    transform: translateX(-100px);
                    opacity: 0;
                }

                to {
                    transform: translateX(0%);
                    opacity: 1;
                }
            }
        }
    }

    p:nth-child(3) {
        animation: movsThree .8s ease-in-out;

        @keyframes movsThree {
            from {
                transform: translateX(100px);
                opacity: 0;
            }

            to {
                transform: translateX(0%);
                opacity: 1;
            }
        }

        @media (max-width: 991px) {
            transition: 1s ease-in-out;
            width: 100%;
            font-size: 14px;

            @keyframes movsThree {
                from {
                    transform: translateX(-100px);
                    opacity: 0;
                }

                to {
                    transform: translateX(0%);
                    opacity: 1;
                }
            }
        }
    }

}