.CT-360-tour-image-feature {
    grid-column: 1 / 2;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    animation: toue360Animation 1.5s ease-out;

    @keyframes toue360Animation {
        from {
            transform: scale(0);
        }

        to {
            transform: scale(1);
        }
    }

    @media (max-width: 991px) {
        grid-column: 1 / 3;
        grid-row: 2/3;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 1;
    }
}

.tourImg-holder {
    width: 100%;

    @media (max-width: 991px) {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 8px;
    }
}

.CT-360Img {
    width: 100%;
    height: 600px !important;

    @media (max-width: 991px) {
        height: 100% !important;
        width: 100% !important;
    }
}