.CT-our-vision-holder {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;

    // z-index: -2;
    @media (min-width: 280px) and (max-width: 1200px) {
        @media (max-width: 991px) {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr;
            border-radius: 0px !important;
        }
    }
}