// 360 Video Holder SM Screen
.CT-galleryHolder {
  aspect-ratio: 5 / 4; }
// List Of gallery Holder
.CT-list-gallery-holder {
  // display: flex
  padding-bottom: 32px;
  // List Of gallery Grid
  .CT-list-gallery-grid {
    position: sticky;
    inset: 0px 0px;
    width: 110px; }
  .CT-list-gallery-grid-button {
    min-width: 115px;
    height: 111px;
    text-align: justify;
    text-justify: auto;
    margin: 0; }
  .CT-gallery-grid-sm-holder {
    min-width: 120px;
    min-height: 111px;
    text-align: center;
    // justify-content: space-between
 } }    // margin-right: 16px
//  Property Gallery LG Screen
.CT-gallery-holder {
  padding: 24px 0px;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 24px;
  // 360 Video
  .CT-virtual-360 {
    grid-column: 1 / 3;
    grid-row: 1 / 4;
    aspect-ratio: 5/4; }
  // Image Holder Section
  .CT-prop-gallery-grid {
    grid-column: 3/5;
    grid-row: 1/4;
    display: grid;
    grid-gap: 24px;
    grid-template-columns: auto;
    grid-template-rows: repeat(3, 1fr);
    // prop-mini-card
    .prop-mini-card {
      grid-column: 1/3;
      grid-row: 1/3;
 }      // padding-top: 80.25%
    .prop-mini-card-2 {
      // padding-top: 80.25%
      grid-column: 3/5;
      grid-row: 1/3; }
    // prop-small-card
    .prop-small-card {
      grid-column: auto;
      grid-row: 3/4; } }
  // Button LG
  .CT-btnPosition {
    position: absolute;
    bottom: 2%;
    right: 1%;
    height: max-content;
    width: max-content; } }
