.CT-z-Index {
  z-index: 9000; }
.CT-adv-search {
  backdrop-filter: saturate(180%) blur(20px);
  background-image: linear-gradient(to right,rgba(233, 247, 254, .80) , rgba(243, 243, 255, .80));
  // background-color: rgba(70, 73, 76, .55)
  max-height: calc( 100vh - 80px );
  min-height: calc( 0vh - 80px ); }
.CT-height-holder {
  max-height: calc( 100vh - 80px );
  min-height: calc( 0vh - 80px ); }


.CT-show-blur {
  z-index: 99999;
  backdrop-filter: saturate(100%) blur(50px);
  background-color: rgba(68, 68, 68, 0.5);
  // background-color: rgba(255, 255, 255, 0.5)
  // background-image: linear-gradient(to right,rgba(233, 247, 254, .5) , rgba(243, 243, 255, .5))
 }  // background-color: rgba(156, 157, 254, .1)

.CT-hide-blur {
  z-index: -1;
  background-color: rgba(70, 73, 76, 0);
  backdrop-filter: saturate(100%) blur(0px); }
.CT-hide-holder {
  top: -500px;
  opacity: 0; }
.CT-show-holder {
  top: 0;
  opacity: 1; }
.CT-blur-Z-index {
  z-index: 7000; }


