// Main Grid Holder
.CT-property-holder {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr 350px;
    align-items: center;
    justify-content: center;
}

// Text Holder
.CT-connect-text-holder {
    width: 100%;
    grid-column: 1/2;
    grid-row: 1/3;

    @media screen and (max-width: 860px) {
        width: 100%;
        grid-column: 1/3;
        grid-row: 2/3;
        font-size: 1.25rem;
        text-align: center;
    }
}

// Image Holder
.CT-connect-image-holder {
    width: 100%;
    height: 100%;
    grid-column: 2/3;
    grid-row: 1/3;

    @media screen and (max-width: 860px) {
        max-width: 100%;
        grid-column: 1/3;
        grid-row: 1/2;
    }
}

/* Blur Blue Image */
.CT-layer-I {
    width: 87%;
    height: 100%;
    bottom: 0%;
    left: 7%;
    z-index: 1;

    @media screen and (max-width: 860px) {
        width: 87%;
        height: 77%;
        bottom: 12%;
        left: 6%;
        z-index: 1;
    }
        animation: layer-I 3s ease-in-out infinite ;
        @keyframes layer-I {
            0% {
                transform: scale(1);
            }
            50%{
                transform: scale(.95);
            }

            100% {
                
                transform: scale(1);
            }
        }

}

/* Main Image */
.CT-layer-IV {
    width: 100%;
    height: 100%;
    bottom: 0%;
    left: 0%;
    z-index: -1;
    @media screen and (max-width: 860px) {
        width: 100%;
        height: 23rem;
        bottom: -16%;
        left: 2%;
        z-index: -1;
    }
    animation: layer-IV 2s ease-in-out;

    @keyframes layer-IV {
        0% {
           opacity: 0;
        }

        100% {
           opacity: 1;
        }
    }
}

// Layer Two Blue
.CT-layer-II {
    width: 75%;
    height: 100%;
    bottom: 0%;
    left: 0%;
    z-index: -1;

    @media screen and (max-width: 860px) {
        width: 75%;
        height: 100%;
        bottom: 4%;
        left: 0%;
        z-index: -1;
    }
    animation: layer-II 5s ease-in-out infinite;
    @keyframes layer-II {
        0% {
            transform: scale(1);
            transform: rotate(0deg);
        }
    
        50% {
            transform: scale(.9);
            transform: rotate(10deg);
        }
     
        100% {
            transform: scale(1);
            transform: rotate(0deg);
        }
    }
}

// Layer Two Pink
.CT-layer-III {
    width: 70%;
    height: 100%;
    bottom: 8%;
    left: 19%;
    z-index: -1;

    @media screen and (max-width: 860px) {
        width: 77%;
        height: 100%;
        bottom: 10%;
        left: 13%;
        z-index: -1;
    }
        animation: layer-III 3s ease-in-out infinite;

        @keyframes layer-III {
            0% {
                transform: scaleY(1);
            }

            50% {
                transform: scaleY(0.95);
            }

            100% {
                transform: scaleY(1);
            }
        }
}

// Line Group Holder
.CT-connect-line {
    background-size: contain;
    background-position: 20%;
    background-repeat: no-repeat;
    z-index: -1;

    @media screen and (max-width: 860px) {
        background-size: 35%;
        background-position: 29% 28%;
        background-repeat: no-repeat;
        z-index: -1;
    }
}

// Button Holder
.CT-button-holder {
    display: flex;

    @media screen and (max-width: 860px) {
        justify-content: center;
    }
}