.CT-holder{
    height: 950px;
}
.CT-holder-blur{
    backdrop-filter: saturate(180%) blur(2px);
  background-image: linear-gradient(to right,rgba(233, 247, 254, .20) , rgba(243, 243, 255, .40))
}
.CT-search-header{
  top: 40px;
  height: 80px;
}
.CT-adv-search-holder{
  top: -40px;
}
.CT-search-footer{
  bottom: 40px;
  height: 80px;
  z-index: -1;
}
.CT-adv-search{
  padding-top: 40px;
  top: -45px;
}
.CT-adv-search-mover{
  z-index: 1;
  transition: ease-in-out all .2s;
}
.CT-Gradian{
   background-image: linear-gradient(to left,#4bbef7b2 , #9c9dfe )
}
.CT-main{
    height: 80px;
    z-index: 2;
}
.CT-childe{
    height: 40px;
    z-index: 2;
}
.CT-counters:hover{
   background-color: rgba(156, 157, 254, .16) ;
}
.CT-selected{
   background-color: rgba(156, 157, 254, .16) ;
}
.CT-selected-borderd{
   border: solid 2px #9C9DFE;
}
.CT-px-32{
   padding: 0 32px;
}
.CT-pl-32{
   padding: 0 0 0 32px;
}
.CT-f-radius-A{
  border-radius: 80px;
}
.CT-l-radius-A{
  border-radius: 80px 0 0 80px;
}
.CT-r-radius-A{
  border-radius: 0 80px 80px 0 ;
}
.CT-f-radius-B{
  border-radius: 40px !important;
}
.CT-b-radius-b{
  border-radius: 0 0 40px 40px;
}
.CT-border-white{
  border: solid 4px rgb(255, 255, 255);
}
.CT-f-radius-search-header{
  border-radius: 40px 16px 0 0;
}
.CT-f-radius-search-footer{
  border-radius: 0 0 40px 16px;
}
.CT-main-sm-containers{
  flex-grow: 1;
}
.CT-sm-containers{
  flex-grow: 1;
    // fixed width 
  max-width: 254px;
}
.CT-main-lg-containers{
  flex-grow: 2;
}
.CT-lg-containers{
  flex-grow: 2;
  max-width: 535px;
}
.CT-min-max{
  max-width: 200px;
}
.CT-sm-containers:hover, .CT-lg-containers:hover{
  background-color: rgba(156, 157, 254, .16) ;
}
.CT-search-icon-holder{
  height: 64px;
  width: 64px;
}
// adv search Elements
.CT-h{
  height: 80px;
}
.CT-main-border{
  border: solid 2px rgba(78, 79, 185, 0.1) ;
}
.CT-main-border:hover{
  border: solid 2px #9C9DFE;
}
.CT-bold-sF{
  font-size: 29px;
    font-family: Roboto-Bold;
    font-style: Bold;
}
.CT-adv-btn{
  display: inline-block;
    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}