.CT-Controller-Map-Holder {
  position: absolute !important;
  top: calc(100% - 73%) !important; }
.CT-Holder-Property-Selection {
  z-index: 1;
  animation: breathing 1s ease-in-out infinite;
  @keyframes breathing {
    0% {
      transform: translateY(5px); }

    50% {
      transform: translateY(10px); }

    100% {
      transform: translateY(5px); } } }
