.CT-overview-hold {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 25px);
    justify-content: center;
    align-items: center;
    text-align: center;
    grid-gap: 16px 24px;
    @media ( max-width: 992px ) {
        grid-template-columns: repeat(3, 1fr); }
    @media ( max-width: 575px ) {
        grid-template-columns: repeat(2, 1fr); } }
