.aCT-wire-frame-loading {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.01) 0%, rgba(0, 0, 0, 0.11) 20%, rgba(0, 0, 0, 0.01) 40%, rgba(0, 0, 0, 0.01) 100%);
    background-size: 800px 204px;
    animation: loading 1s linear infinite forwards;

    @keyframes loading {
        0% {
            background-position: -468px 0;
        }

        100% {
            background-position: 468px 0;
        }
    }
}

.aCT-wire-frame-loading-agencies {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.01) 0%, rgba(0, 0, 0, 0.11) 20%, rgba(0, 0, 0, 0.01) 40%, rgba(0, 0, 0, 0.01) 100%);
    background-size: 800px 240px;
    animation: agenciesLoading 1s linear infinite forwards;

    @keyframes agenciesLoading {
        0% {
            background-position: -468px 0;
        }

        100% {
            background-position: 468px 0;
        }
    }
}

.aCT-wire-frame-loading-cover {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.01) 0%, rgba(0, 0, 0, 0.11) 20%, rgba(0, 0, 0, 0.01) 40%, rgba(0, 0, 0, 0.01) 100%);
    width: 100%;
    background-size: 1700px 740px;
    animation: coverLoading 2s linear infinite forwards;

    @keyframes coverLoading {
        0% {
            background-position: -1700px 0;
        }

        100% {
            background-position: 1700px 0;
        }
    }
}

.aCT-wire-frame-opacity {
    :nth-of-type(1) {
            clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
            background-color: #d4d6e0;
            animation: slide 1.8s -1.2s ease-in-out infinite;
        }
        :nth-of-type(2) {
        clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
        background-color: #d4d6e0;
        animation: slide 1.8s -0.9s ease-in-out infinite;
    }
    :nth-of-type(3) {
        background-color: #d4d6e0;
        clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
        animation: slide 1.8s -0.6s ease-in-out infinite;
    }
    :nth-of-type(4) {
        clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
        animation: slide 1.8s -0.3s ease-in-out infinite;
        background-color: #d4d6e0;
    }
    :nth-of-type(5) {
        clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
        background-color: #d4d6e0;
        animation: slide 1.8s ease-in-out infinite;
    }
    @keyframes slide {
        0% {opacity: 1;}

        50% {opacity: 0.5;}

        80% {opacity: 0;}

        100% {opacity: 1;}
    }
}