.CT-parent-holder {
  min-height: calc(100vh - 80px);
 }  // position: relative !important

.CT-Properties-Holder {
  // min-height: calc(10vh - 10px)
  position: relative;
  transition: ease-in-out 400ms margin , ease-in-out 400ms right;
  width: 50% !important;
  // @media (min-width: 992px)
  //   width: 60vw !important
  // @media (min-width: 1440px)
 }  //   width: 60% !important


.CT-Properties-Holder-sm {
  // min-height: calc(100vh - 80px)
  min-height: calc(100vh - 140px);
  position: absolute;
  transition: ease-in-out 400ms top;
  z-index: 8000; }

.CT-MapView-Parent-Holder {
  height: calc(100vh - 80px);
  position: sticky !important;
  top: 80px;
 }  // overflow: hidden !important

.CT-btnToggle-Map {
  z-index: 2;
  position: absolute;
  left: 10px;
  top: 10px; }

.CT-FilterBTN-Orange {
  background-color: orange; }

.CT-Border {
  transition: ease-in-out 200ms;
  border: 1px solid #b0b0b063;
  &:hover {
    border: 1px solid #00000062; } }
