.CT-who-we-are-title-grid {
    grid-column: 1 / 2;
    grid-row: 1/3;
    line-height: 1.09;
    border-radius: 20px 0 0 20px;

    // Who We Are Title Grid Small Screen
    @media (max-width: 991px) {
        grid-column: 1 / 2;
        grid-row: 1/2;
        line-height: 1.09;
        border-radius: 0px;
    }
}

.CT-bold-88 {
    font-size: 88px;
    font-family: Roboto-Bold;
    font-style: normal;
    text-shadow: 2px 2px 7px rgba(0, 0, 0, 0.16);

    @if screen {
        @media (min-width: 0px) and (max-width: 609px) {
            font-size: 32px;
        }
    }

    @if screen {
        @media (min-width: 500px) and (max-width: 991px) {
            font-size: 64px;
        }
    }
}

.CT-bold-144 {
    font-size: 144px;
    font-family: Roboto-Bold;
    font-style: normal;
    text-shadow: 2px 2px 7px rgba(0, 0, 0, 0.16);

    // Small Screen
    @if screen {
        @media (min-width: 0px) and (max-width: 609px) {
            font-size: 32px;
        }
    }

    // Mid Screen
    @if screen {
        @media (min-width: 500px) and (max-width: 991px) {
            font-size: 64px;
        }
    }

    // Large Screen
    @if screen {
        @media (min-width: 992px) and (max-width: 1200px) {
            font-size: 120px;
        }
    }
}

.CT-whoWeAre-title {
    width: 100%;
    padding-left: 48px;

    @media (max-width: 991px) {
        height: auto;
        display: flex;
        justify-content: center;
        align-items: start;
        padding: 48px 0px 0px 0px;
        margin-bottom: auto;

        p:nth-of-type(1) {
            padding-right: 8px;
        }

        p:nth-of-type(2) {
            padding-right: 8px;
        }
    }


    p:nth-of-type(1) {
        animation: whoAnimtaion 1.4s ease-in-out;

        @keyframes whoAnimtaion {
            from {
                transform: translateY(-400px);
                opacity: 0;
            }

            to {
                transform: translateX(0%);
                opacity: 1;
            }
        }
    }

    p:nth-of-type(2) {
        animation: weAnimtaion 1.4s linear;

        @keyframes weAnimtaion {
            0% {
                opacity: 0;
                filter: blur(20px);
                transform: translateX(-100px);
            }

            50% {
                opacity: 1;
                filter: blur(10px);
                transform: translateX(50px);
            }

            100% {
                opacity: 1;
                filter: blur(0px);
                transform: translateX(0px);
            }
        }
    }

    p:nth-of-type(3) {
        animation: areAnimtaion 1.4s ease-in-out;

        @keyframes areAnimtaion {
            from {
                transform: translateY(400px);
                opacity: 0;
            }

            to {
                transform: translateX(0%);
            }
        }
    }
}