@import "./utilities";
@import "./vars";

@font-face {
    font-family: Roboto-Bold;
    src: url('../assets/fonts/Roboto-Bold.ttf'); }
@font-face {
    font-family: Roboto-Light;
    src: url('../assets/fonts/Roboto-Light.ttf'); }
@font-face {
    font-family: Roboto-Medium;
    src: url('../assets/fonts/Roboto-Medium.ttf'); }
@font-face {
    font-family: Roboto-Regular;
    src: url('../assets/fonts/Roboto-Regular.ttf'); }
body {
    font-family: Roboto-Regular; }

// Font BOLD
.fCT-bold-sA {
    font-size: 12px;
    font-family: Roboto-Bold;
    font-style: normal; }
.fCT-bold-sB {
    font-size: 14px;
    font-family: Roboto-Bold;
    font-style: normal; }
.fCT-bold-sC {
    font-size: 16px;
    font-family: Roboto-Bold;
    font-style: normal; }
.fCT-bold-sD {
    font-size: 18px;
    font-family: Roboto-Bold;
    font-style: normal; }
.fCT-bold-sE {
    font-size: 20px;
    font-family: Roboto-Bold;
    font-style: normal; }
.fCT-bold-sF {
    font-size: 24px;
    font-family: Roboto-Bold;
    font-style: normal; }
.fCT-bold-G {
    font-size: 40px;
    font-family: Roboto-Bold;
    font-style: normal; }
.fCT-bold-54 {
    font-size: 56px;
    font-family: Roboto-Bold;
    font-style: normal; }
.fCT-bold-64 {
    font-size: 64px;
    font-family: Roboto-Bold;
    font-style: normal; }

// Font Regular
.fCT-Regular-sA {
    font-size: 12px;
    font-family: Roboto-Regular; }
.fCT-Regular-sB {
    font-size: 14px;
    font-family: Roboto-Regular; }
.fCT-Regular-sC {
    font-size: 16px;
    font-family: Roboto-Regular; }
.fCT-Regular-sD {
    font-size: 18px;
    font-family: Roboto-Regular; }
.fCT-Regular-sE {
    font-size: 24px;
    font-family: Roboto-Regular; }
.fCT-Regular-sF {
    font-size: 30px;
    font-family: Roboto-Regular; }

// Font Light
.fCT-Light-sA {
    font-size: 12px;
    font-family: Roboto-Light; }
.fCT-Light-sB {
    font-size: 14px;
    font-family: Roboto-Light; }
.fCT-Light-sC {
    font-size: 16px;
    font-family: Roboto-Light; }
.fCT-Light-sD {
    font-size: 18px;
    font-family: Roboto-Light; }
.fCT-Light-sE {
    font-size: 24px;
    font-family: Roboto-Light; }
// Line Height
.fCT-Line-heightA {
    line-height: 0.5; }
.fCT-Line-heightB {
    line-height: 1; }
.fCT-Line-heightC {
    line-height: 1.9; }

// Wire Frame Paragraph Height
.fCT-wireFrame-hA {
    height: 10px; }
.fCT-wireFrame-hB {
    height: 12px; }
.fCT-wireFrame-hC {
    height: 14px; }
.fCT-wireFrame-hD {
    height: 16px; }
.fCT-wireFrame-hE {
    height: 18px; }
.fCT-wireFrame-hF {
    height: 20px; }
.fCT-wireFrame-hG {
    height: 24px; }

// Small devices (landscape phones, 576px and up)
// @media (min-width: 576px)
// // Medium devices (tablets, 768px and up)
@media (max-width: 768px) {
    // Font BOLD
    .fCT-bold-sA {
        font-size: 10px;
        font-family: Roboto-Bold;
        font-style: normal; }
    .fCT-bold-sB {
        font-size: 12px;
        font-family: Roboto-Bold;
        font-style: normal; }
    .fCT-bold-sC {
        font-size: 14px;
        font-family: Roboto-Bold;
        font-style: normal; }
    .fCT-bold-sD {
        font-size: 16px;
        font-family: Roboto-Bold;
        font-style: normal; }
    .fCT-bold-sE {
        font-size: 20px;
        font-family: Roboto-Bold;
        font-style: normal;
 }        // Font Regular
    .fCT-Regular-sA {
        font-size: 10px;
        font-family: Roboto-Regular; }
    .fCT-Regular-sB {
        font-size: 12px;
        font-family: Roboto-Regular; }
    .fCT-Regular-sC {
        font-size: 14px;
        font-family: Roboto-Regular; }
    .fCT-Regular-sD {
        font-size: 16px;
        font-family: Roboto-Regular; }
    .fCT-Regular-sE {
        font-size: 20px;
        font-family: Roboto-Regular; }
    // Font Light
    .fCT-Light-sA {
        font-size: 10px;
        font-family: Roboto-Light; }
    .fCT-Light-sB {
        font-size: 12px;
        font-family: Roboto-Light; }
    .fCT-Light-sC {
        font-size: 14px;
        font-family: Roboto-Light; }
    .fCT-Light-sD {
        font-size: 16px;
        font-family: Roboto-Light; }
    .fCT-Light-sE {
        font-size: 20px;
        font-family: Roboto-Light; } }
