.CT-imgHeight {
    max-height: 700px;
    min-height: 500px;
    padding-bottom: calc( 100% - (16/9) );
    height: 400px;
    max-width: 100%;
    padding-bottom: calc(0%*9/16); }
.CT-text-holder {
    max-height: 80px;
    overflow: auto; }

.boxContainer {
    padding-bottom: 24px;
    display: grid;
    background-color: #ffff00;
    aspect-ratio: 5/3;
    grid-template-rows: 56px 100% max-content;
    position: relative; }

