// How It Work Grid System
.CT-howItWork-holder {
    display: grid;
    max-width: 100%;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, minmax(auto, 1fr));
    background-repeat: #080;
    grid-gap: 24px;

}
//  Head Title Grid Position
.CT-head-title {
    height: max-content;
    grid-column: 1/2;
    grid-row: 1/2;
}
// Main Grid Holder
.CT-details-holder {
    @media (min-width: 1130px) {
        grid-column: 1/2;
        grid-row: 2/3;
        max-width: 100%;
        height: 100%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 24px;
    }
}
// Image 
.CT-work-image {
    @media (max-width: 1130px) {
        margin-bottom: 24px;
    }
    border-radius: 20px;
    padding-bottom: 56.25%;
}
// Section Grid Holder
.CT-work-description {
    @media (min-width: 520px) {
        max-width: 100%;
        display: grid;
        grid-gap: 24px;
        grid-row: 1/2;
        grid-column: 1/2;
    }
}
// Section Holder & Icon
.CT-description-item {
    min-width: 0; // to resolve an issu
    display: flex;
    align-items: center;
    @media (max-width: 520px) {
        width: max-content;
        display: block;
        margin: auto;
        margin-bottom: 16px;
    }
}
// Text Holder
.CT-text-section-holder{
   @media (min-width: 521px) {
    margin-left: 24px;
    text-align: left;
    :last-child{
        margin-left: 16px;
    }
   }
}
// Line Gruop Holder
.CT-work-line {
    background-position-x: 55%;
    background-position-y: -20%;
    background-repeat: no-repeat;
}