.CT-how-it-works-container{
    display: grid;
    grid-template-rows: minmax(700px,auto);
    grid-gap: 0px 48px;
    grid-template-areas: 
    "connect-prop" "ournumber" "how-work" "control" "our-clients" "contact-us" "drop-info" ;
}
.CT-connect-with-property {
    grid-area: connect-prop;
}
.CT-our-numbers{
    grid-area: ournumber;
}
.CT-how-works{
    text-align: center;
    grid-area: how-work;
}
.CT-sophisticated-control{
    grid-area: control;
}
.CT-our-clients{
    grid-area: our-clients;
    display: grid;
    text-align: center;
    grid-gap: 24px;
    justify-content: center;
    align-items: center;
    grid-template-columns: repeat(1, minmax(339px, auto));
}

.CT-contact-us{
    grid-area: contact-us;
}
.CT-drop-information{
    grid-area: drop-info;
}
.CT-font-bold {
    font-size: 56px;
    font-family: Roboto-Bold;
    font-style: normal;

    @media (max-width: 991px) {
        font-size: 2.5rem;
    }

    @media (max-width: 600px) {
        font-size: 1.5rem;
    }

    @media (max-width: 390px) {
        font-size: 1.125rem;
    }
}