.CT-aboutus-image {
    display: block;
    width: 100%;

    @media (min-width: 992px) {
        height: 700px;
    }

    @media (max-width: 991px) {
        width: 100%;
        height: calc(100vh - 400px);
    }

}

.CT-image-feature {
    grid-column: 2 / 3;
    max-width: 100%;

}