//  Main About Us grid
.CT-aboutus-and-Indicators-grid {
    @media (min-width: 992px) {
        display: grid;
        gap: 24px;
        max-width: 100%;
        height: 100%;
        justify-content: center;
        align-items: center;
        grid-template-columns: 1fr 7fr 1fr;
        grid-template-rows: 70px minmax(680px, calc(100vh - 280px)) 70px;
    }
}

.CT-grid-window {
    @media (max-width: 991px) {
        grid-gap: 0;
        gap: 0;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: calc(100vh - 130px) 1fr; // window
    }
}

.CT-grid-ios {
    @media (max-width: 991px) {
        grid-gap: 0;
        gap: 0;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 67vh 1fr; // ios
    }
}

.CT-grid-android {
    @media (max-width: 991px) {
        grid-gap: 0;
        gap: 0;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 77vh; // android
    }
}

// About Us Sections Large Screen
.CT-aboutus-text-image-holder {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 80px 1fr 80px;
}

// About Us Sections Large Screen
.CT-aboutus-text-image-sm-holder {
    grid-column: 1 / 4;
    grid-row: 1 / 3;

    @media (max-width: 991px) {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
        overflow: hidden;

    }
}

.CT-ourportfolio-grid {
    grid-column: 1/4;
    grid-row: 1/4;
}

.CT-indecators-holder {
    grid-column: 3/4;
    grid-row: 2/3;

    // Indecators Holder Small Screen
    @media (max-width: 991px) {
        grid-column: 1/2;
        grid-row: 2/3;
    }
}

.CT-about-mouse {
    grid-column: 1/4;
    grid-row: 3/4;
    cursor: none;
    animation: shake 0.9s infinite;

    @keyframes shake {
        0% {
            transform: translateY(5px)
        }

        50% {
            transform: translateY(10px)
        }

        100% {
            transform: translateY(5px)
        }
    }
}

.CT-about-ourClient-holder {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px 1fr 80px;
    align-items: center;
    justify-content: center;
    gap: 24px;

    @media (max-width: 991px) {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        overflow-y: scroll;
        overflow-x: hidden;
    }
}

.CT-about-ourClient-text {
    @media (min-width: 540px) {
        grid-row: 1/2;
        text-align: center;
        // margin-top: 24px;
    }
}

.CT-about-ourClient-slider {
    grid-row: 2/3;
    height: 100%;
    position: relative;


}

.CT-about-ourClient-shape {
    width: 100%;
    height: 100%;
    background-image: url('../../../../../assets/images/ourClientsShape.svg');
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    z-index: -1;
    animation: ourClientShape 2.5s linear infinite;

    @keyframes ourClientShape {
        0% {
            transform: scaleY(0.95);
        }

        50% {
            transform: scaleY(1);
        }

        100% {
            transform: scaleY(0.95);
        }
    }
}

.CT-about-ourClient-shapeII {
    width: 100%;
    height: 100%;
    background-image: url('../../../../../assets/images/ourClientsShapeI.svg');
    background-position: 35% 50%;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    z-index: -2;
    animation: ourClientShapeII 2.8s linear infinite;

    @keyframes ourClientShapeII {
        0% {
            transform: scaleY(0.95);
        }

        50% {
            transform: scaleY(1);
        }

        100% {
            transform: scaleY(0.95);
        }
    }
}

.CT-bold-64 {
    font-size: 64px;
    font-family: Roboto-Bold;
    font-style: normal;

    @media (max-width: 991px) {
        font-size: 24px;
    }
}

.CT-about-ourClient-button-grid {
    grid-row: 3/4;
}