.slider-holder li {
  box-sizing: content-box !important;
  flex: 0 1 auto !important;
  width: 8px !important;
  height: 8px !important;
  margin-right: 3px !important;
  margin-left: 3px !important;
  text-indent: -999px !important;
  cursor: pointer !important;
  background-color: #fefefe !important;
  opacity: .5 !important;
  transition: opacity .6s ease !important;
  border-radius: 50% !important; }

.carousel-control-prev-icon , .carousel-control-next-icon {
  display: inline-block;
  width: 15px !important;
  height: 15px !important;
  background: 50%/100% 100% no-repeat; }
.CT-property-section-holder {
  bottom: -25px; }
.CT-z-index {
  z-index: 2; }
.CT-love-icon-holder {
  top: 10px;
  right: 10px; }
.slider-holder .active {
  background-color: #414141 !important; }
.fullscreen {
  width: 100%;
  height: auto;

  .button {
    display: inline-block;
    border-radius: 4px;
    background-color: #f4511e;
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 28px;
    padding: 20px;
    width: 200px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px; } }

.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;

  &:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s; } }

.button:hover span {
  padding-right: 25px; }

.button:hover span:after {
  opacity: 1;
  right: 0; }
.CT-Holder-Agency {
  // max-width: 291px
  width: 100%; }
.CT-Text-ellipsis-2Line {
  // white-space: nowrap;
  // overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden; }

.LinkElemStyle:hover {
  color: #46494c; }
