.CT-tooltip{
    z-index: 99999 !important;
    opacity: .98 !important;
}
    
.tooltip-inner{
    max-width: 100% !important;
    padding: 0 !important;
    background-color: inherit !important;    
}
.tooltip .arrow::before{
    border-bottom-color: white !important;
}
.CT-tooltip-BTN{
    &:hover {
        background-color: inherit !important;
        color: inherit !important;
    }
    &:active {
        background-color:inherit !important;
        color: inherit !important;
    }
    &:focus {
        background-color:inherit !important;
        color: inherit !important;
    }
}