.CT-360-tour-page-holder {
    display: grid;
    gap: 24px;
    height: 100%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    position: relative;
    overflow: hidden;
    border-radius: 24px;

    @media (max-width: 991px) {
        border-radius: 0px;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 2fr;
        gap: 0px;
    }
}

.CT-360-line-I {
    width: 100%;
    height: 35%;
    background-image: url('../../../../assets/images/360LineUp.svg');
    background-position: 100% 54%;
    background-repeat: no-repeat;

    @media (min-width: 1200px) and (max-width: 1270px) {
        background-position: 110% 0%;
    }

    @media (max-width: 991px) {
        background-image: unset;
    }

    animation: line-I 1.5s ease-out;

    @keyframes line-I {
        from {
            transform: perspective(500px) translate3d(800px, -650px, 400px);
        }

        to {
            transform: perspective(0px) translate3d(0px);
        }
    }
}

.CT-360-line-II {
    width: 34%;
    height: 100%;
    background-image: url('../../../../assets/images/360LineBottom.svg');
    background-position: bottom left;
    top: 15%;
    left: -6%;
    background-repeat: no-repeat;

    @media (max-width: 991px) {
        background-image: unset;
    }

    animation: line-II 1.5s ease-out;

    @keyframes line-II {
        from {
            transform: perspective(500px) translate3d(-500px, 500px, -100px);
        }

        to {
            transform: perspective(0px) translate3d(0px);
            opacity: 1;
        }
    }
}