.CT-who-we-are-content-grid {
    grid-column: 2 / 3;
    grid-row: 1/3;
    border-radius: 0 20px 20px 0px;

    // Who We Are Content Grid Small Screen
    @media (max-width: 991px) {
        width: 100%;
        height: 100%;
        grid-column: 1/3;
        grid-row: 2/3;
        border-radius: 0px 0px 0px 0px;
    }
}

// Who We Are Content Holder
.CT-whoWeAre-content-holder {
    width: 75%;
    margin-left: auto;
    padding: 0px 48px 48px 0;
    text-align: right;

    //  Small Screen
    @if screen {
        @media (min-width: 0px) and (max-width: 609px) {
            width: 100%;
            margin: auto 0px;
            padding: 24px 16px 0px 16px;
            text-align: center;
        }
    }

    //  Large Screen
    @if screen {
        @media (min-width: 610px) and (max-width: 991px) {
            width: 80%;
            margin: auto 0px;
            padding: 24px 16px 0px 16px;
            text-align: center;
        }
    }
}

// Who We Are Content Holder
.CT-whoWeAre-description {
    font-size: 24px;
    font-family: Roboto-Light;

    // Small Screen
    @media (max-width: 991px) {
        font-size: 16px;
        font-family: Roboto-Light;
        height: 100%;
        display: flex;
        align-items: flex-end;
        justify-content: center;
    }

    // Large Screen
    @if screen {
        @media (min-width: 992px) and (max-width: 1200px) {
            font-size: 18px;
        }
    }

    animation: wrucontentAnimation 2s ease-in-out;

    @keyframes wrucontentAnimation {
        0% {
            opacity: 0;
            transform: translateX(100px);
        }

        50% {
            transform: translateX(50px);
        }

        100% {
            opacity: 1;
            transform: translateX(0px);
        }
    }
}