.CT-Indicators {
  width: 40px;
  height: 40px;
  background-color: rgba(128, 128, 128, .25);
  border-radius: 50%;
  cursor: pointer;

  @media (max-width: 991px) {
    width: 30px;
    height: 30px;
  }
}

.CT-Indicators-holder {
  top: calc(50% - 180px);
  display: grid;
  grid-column: 3/4;
  gap: 24px;
  transition: 1s ease;

  @media (max-width: 991px) {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-template-rows: 1fr;
    gap: 8px;
  }
}