// colors Values
$main: #9c9dfe; //color (Moove)
$secondary: #4bbef7; //color (BLUE)
$support1: #e9f7fe; //color ( White)
$support2: #f5f5ff; //color (Dark BLUE)
$smoke: #eef0fc; //color (Light White)
$dark: #46494c; //color (Dark BLUE)
$gray: #8a8f93; //color (light BLUE)
$green: #2bca7a; //color (Shadow Green)
$white: #ffffff; //color (Shadow Green)
$gold: #F9C80E; //color (Shadow Green)
$orange: #FFBC08; //color (orange)

//Wire Frame Layout Colors
$layout-I: #e2e4ef;
$layout-II: #d4d6e0;
$layout-III: #6C757D;
$layout-IV: #343A40;

// opacity Colors Value
$opacity-gray-5: rgba(138, 143, 147, 0.5); //color (Opacity Gray)
$opacity-green-10: rgba(43, 202, 122, 0.1); //color (Opacity Green)
$opacity-green-20: rgba(43, 202, 122, 0.2); //color (Opacity Green)
$opacity-white-20: rgba(255, 255, 255, 0.2); //color (Opacity white)
$opacity-white-40: rgba(255, 255, 255, 0.4); //color (Opacity white)
$opacity-secondary-10: rgba(75, 190, 247, 0.1);
$opacity-secondary-20: rgba(75, 190, 247, 0.25);
$opacity-main-10: rgba(156, 158, 254, 0.10); //color (Opacity main)
$opacity-main-20: rgba(156, 158, 254, 0.25); //color (Opacity main)
$opacity-main-40: rgba(212, 228, 255, 0.5); //color (Opacity main)
$opacity-danger-10: rgba(255, 53, 53, 0.1); //color (Opacity main)

body {
  background-color: $smoke; }

// Backgrounds ===START
.cCT-main-bg {
  background-color: $main; }
.cCT-secondary-bg {
  background-color: $secondary; }
.cCT-support1-bg {
  background-color: $support1; }
.cCT-support2-bg {
  background-color: $support2; }
.cCT-smoke-bg {
  background-color: $smoke; }
.cCT-white-bg {
  background-color: $white; }
.cCT-dark-bg {
  background-color: $dark; }
.cCT-gray-bg {
  background-color: $gray; }
.cCT-green-bg {
  background-color: $green; }
.cCT-gold-bg {
  background-color: $gold; }
.cCT-orange-bg {
  background-color: $orange; }
.cCT-inherit-bg {
  background-color: inherit; }
.cCT-none-bg {
  background-color: rgba(255, 255, 255, 0) !important; }

// Layout Wire Fare ===START
.cCT-layout-I {
  background-color: $layout-I; }
.cCT-layout-II {
  background-color: $layout-II; }
.cCT-layout-III {
  background-color: $layout-III; }
.cCT-layout-IV {
  background-color: $layout-IV; }

// background opacity
@mixin cCT-main-bg($alpha) { //color (baby BLUE)
  background-color: rgba(50,167,226, $alpha); }
@mixin cCT-smoke-bg($alpha) { //color (White)
  background-color: rgba(250,250,250, $alpha); }
@mixin cCT-gray-bg($alpha) { //color (White)
  background-color: rgba(138,143,147, $alpha); }
.cCT-opacity-green-bg-10 { // color (green)
  background-color: $opacity-green-10; }
.cCT-opacity-green-bg-20 { // color (green)
  background-color: $opacity-green-20; }
.cCT-opacity-main-bg-10 {
  background-color: $opacity-main-10; }
.cCT-opacity-main-bg-20 {
  background-color: $opacity-main-20; }
.cCT-opacity-main-bg-40 {
  background-color: $opacity-main-40; }
.cCT-opacity-secondary-bg-10 {
  background-color: $opacity-secondary-10; }
.cCT-opacity-secondary-bg-20 {
  background-color: $opacity-secondary-20; }
.cCT-opacity-white-bg-40 {
  background-color: $opacity-white-40; }
.cCT-opacity-danger-bg-10 {
  background-color: $opacity-danger-10; }

// Backgrounds ===END

// Gradian Linear ===START
.cCT-Gradian-Linear-Main {
  background-image: linear-gradient(to right,$secondary , $main); }
.cCT-Gradian-Linear-mid-Main {
  background-image: linear-gradient(to right,rgba(75, 190, 247, .25) , rgba(156, 157, 254, .25)); }
.cCT-Gradian-Linear-supp {
  background-image: linear-gradient(to right,$support1 , $support2); }
.cCT-Gradian-Linear-supp2 {
  background-image: linear-gradient(to right,$support2 , $support1); }
.cCT-Gradian-Linear-Gold {
  background-image: linear-gradient(to right,rgb(230, 210, 139) ,#ffc600); }
.cCT-Gradian-Linear-Opacity-BW {
  background-image: linear-gradient(to right,#3f4244,rgba(138, 143, 147, 0.0)); }
// Gradian Linear ===END

// TextColor ===START
.cCT-main-text {
  color: $main; }
.cCT-secondary-text {
  color: $secondary; }
.cCT-support1-text {
  color: $support1; }
.cCT-support2-text {
  color: $support2; }
.cCT-smoke-text {
  color: $smoke; }
.cCT-dark-text {
  color: $dark; }
.cCT-white-text {
  color: $white; }
.cCT-gray-text {
  color: $gray !important; }
.cCT-green-text {
  color: $green; }
.cCT-gold-text {
  color: $gold; }
.cCT-orange-text {
  color: $orange; }
// TextColor ===END

.cCT-login-checkBox {
  &:checked + p {
    color: $secondary; } }
.cCT-Main-Hover {
  &:hover {
    color: $secondary; } }
